.n-search {
	position: relative;
	@include clr;
	&__field {
		overflow: hidden; }
	&__aside {
		float: right;
		padding-left: 20px; }
	&__btn {
		display: inline-block;
		vertical-align: top;
		padding: 0;
		border-radius: 3px;
		width: 160px; }
	&__btn,
	&__link {
		height: 53px;
		line-height: 53px;
		font-size: 16px;
		text-align: center; } }
